import React, { useRef, useState } from "react";
import Image1 from "../../assets/public/banner-Img-2.jpg";
import { Swiper, SwiperSlide, useSwiper } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import SwiperCore, { Autoplay, Pagination, Navigation } from "swiper";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowLeft, faArrowRight } from "@fortawesome/free-solid-svg-icons";
const Index = () => {
  const products = [
    {
      name: "Foot Drop Splint",
      description:
        "Anatomically designed to maintain the foot in the functional position and ensure perfect weight distribution.",
      image: Image1,
      url: "#aboutussection",
    },
    {
      name: "Contoured LS Corset",
      description:
        "Significantly designed to support and immobilise the lumbosacral region and correct the postural deformity to allay low back pain syndrome.",
      image: Image1,
      url: "#aboutussection",
    },
    {
      name: "Wrist & Forearm Splint",
      description:
        "Designed to immobilise and provide firm but comfortable support to hand and wrist in various orthopedic conditions.",
      image: Image1,
      url: "#aboutussection",
    },
    {
      name: "Hinged Knee Brace Open Paella",
      description:
        "Designed to keep the knee in proper alignment when it bends, helping to heal and avoid further injuries.",
      image: Image1,
      url: "#aboutussection",
    },
    {
      name: "Post Maternity Corset",
      description:
        "Significantly designed to aid in early and effective post-pregnancy recovery and strengthen the abdominal muscles.",
      image: Image1,
      url: "#aboutussection",
    },
    {
      name: "Clavicle Brace",
      description:
        "Designed to keep the collarbone area immobilized after a simple break or fracture, allowing the bone to heal on its own.",
      image: Image1,
      url: "#aboutussection",
    },
    {
      name: "Thoraco Lumbar Appliances (Taylor's Brace) - Regular",
      description:
        "A lightweight spinal brace that covers all dorsal, lumbar, and sacral vertebrae, supporting and immobilizing the spine in a neutral position while still allowing necessary body movement.",
      image: Image1,
      url: "#aboutussection",
    },
  ];
  // SwiperCore.use([Navigation]);

  return (
    <>
      <Swiper
        // spaceBetween={10}
        centeredSlides={true}
        navigation={true}
        modules={[Navigation]}
        className="mySwiper productswiper"
      >
        <div className="container">
          {products.map((product, index) => (
            <SwiperSlide
              key={index}
              style={{ backgroundImage: `url(${product.image})` }}
            >
              <div className="product-details">
                <h3>{product.name}</h3>
                <p className="text-center">{product.description}</p>
                <a href={product.url}>View Details</a>
              </div>
            </SwiperSlide>
          ))}
        </div>
      </Swiper>
    </>
  );
};

export default Index;
