import React, { useState } from "react";
import Loader from "../../components/Loader/index";
import HomeViewModal from "../../viewmodals/Visitor/Home";
import ContactUs from "../../components/contact-us/index";
import ProductSwiper from "../../components/Product-swiper/index";
import PageTitle from "../../components/pageTitle";
import ProductList from "./productList";
import Modal from "../Products/Modal";
import products from "../Products/products.json";
const Home = () => {
  const [selectedProduct, setSelectedProduct] = useState(null);

  const handleItemClick = (product) => {
    setSelectedProduct(product);
  };

  const handleCloseModal = () => {
    setSelectedProduct(null);
  };

  const { loading, navigateAbout } = HomeViewModal();
  return (
    <>
      {loading ? (
        <Loader />
      ) : (
        <>
          <PageTitle title="Home | Divine" />
          <ProductSwiper />
          <div className="section-spacer">
            <div className="container">
              {/* ContactUs Page Start */}
              {/* <ContactUs /> */}
              {/* ContactUs Page Start */}
              {/* AboutUs Page Start */}
              {/* AboutUs Page Start */}
            </div>
            <ProductList products={products} onItemClick={handleItemClick} />
            {selectedProduct && (
              <Modal product={selectedProduct} onClose={handleCloseModal} />
            )}
          </div>
        </>
        /* <button className='btn btn-primary' onClick={()=>{navigateAbout()}}>About</button> */
      )}
    </>
  );
};

export default Home;
