import React from "react";
import { useState, useEffect } from "react";
import { Link } from 'react-router-dom'

const ProductList = ({ products, onItemClick }) => {
  const [mobileMenu, setMobileMenu] = useState(false);

  function onResize() {
    if (typeof window !== undefined) {
      if (window.innerWidth < 1200) {
        setMobileMenu(true);
      } else {
        setMobileMenu(false);
      }
    }
  }

  React.useEffect(() => {
    onResize();
    {
      window.addEventListener("resize", function () {
        onResize();
      });
    }
    // ApiCalled();
  }, []);
  return (

    <div className={mobileMenu === true ? "container" : ""}>
      <div className="product_category_section">
        {Object.keys(products).map((category, index) => (
          <div key={category} className="pt-3">
            <div className="productname_section" id={category}>
              <div className="container">
                <div className="row align-items-center py-2">
                  <div className="col-md-8">
                    <p className="section-title py-2 reverse-title">
                      {category}
                    </p>
                  </div>
                  <div className="col-md-4">
                    <div className="custom-btn position-relative text-end">
                      <Link href="/products" className="py-2 px-5 huge-btn d-inline-block">
                        View All
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="container section-spacer-mini">
              <div className="row align-items-start justify-content-center">
                {products[category]
                  .map((product) => (
                    <div className="col-lg-4 col-md-6 my-2 p-0">
                      <div className="category_section_card mx-2">
                        <div
                          className="card"
                          key={product.name}
                          onClick={() => onItemClick(product)}
                        >
                          <div className="card-body d-flex align-items-center justify-content-center">
                            <img
                              src={product.Image}
                              alt={product.Image}
                              className="pro-image"
                            />
                          </div>
                          <div className="card-footer">
                            <p className="small-tiny-description text-darkess-blue">
                              {product.name}
                            </p>
                            <p className="small-tiny-description"></p>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))
                  .slice(0, 3)}
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* {Object.keys(products).map((category) => (
        <div key={category}>
          <h2>{category}</h2>
          <div className="card-container">
            {products[category].map((product) => (
              <div
                className="card"
                key={product.name}
                onClick={() => onItemClick(product)}
              >
                <h3>{product.name}</h3>
                <p>Price: ${product.price}</p>
              </div>
            ))}
          </div>
        </div>
      ))} */}
    </div>
  );
};

export default ProductList;
